import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Form from 'components/form/Form'
import FormRowControls from 'components/form/FormRowControls'
import EmailField from './EmailField'
import PasswordField from './PasswordField'
import NameField from './NameField'
import { addNewUser, clearSignupForm } from 'modules/signupForm'
import { SIGNUP_FORM_ERROR } from 'modules/signupForm/validators'
import { getisSignupFormValid } from 'selectors'

const SignupForm: React.FC = () => {
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(clearSignupForm())
  }, [ dispatch ])

  const isFormValid = useSelector(getisSignupFormValid)
  const formErrors = isFormValid
    ? []
    : [ SIGNUP_FORM_ERROR ] // TODO Move to state

  const onFormSubmit = async () => {
    if (isFormValid) {
      dispatch(addNewUser())
    }
  }

  return (
    <Form autoComplete='off'>
      <EmailField />

      <PasswordField />

      <NameField />

      <FormRowControls
        errorMessage={formErrors.join(', ')}
        onFormSubmit={onFormSubmit}
      />
    </Form>
  )
}

export default SignupForm
